import React from 'react'
import s from './Friends.module.css'
import {NavLink} from "react-router-dom";



const Friends = (props) => {
    let state= props.friendsPage;
    let friendsDataElements = state.name.map(m => <FriendsItem name={m.name} id={m.id}/>);//masiv
    let messagesDataElements = state.message.map(p => <FriendsMessage message={p.message} id={p.id}/>);//masiv
    return(
        <div className={s.friends}>
        <FriendsItem name={friendsDataElements}/>
        <FriendsMessage message={messagesDataElements}/>
    </div>
    )
}
const FriendsItem =(props)=>{
    debugger
    let path = '/friends/' + props.id;
    return(
        <div>
            <div className={s.items}>
                <NavLink to={path} activeClassName={s.active}>{props.name}</NavLink>
            </div>
        </div>
    )
}
const FriendsMessage =(props)=>{

    return(
        <div>
            <div className={s.friends}>
                <div className={s.messages}>{props.message}</div>
            </div>
        </div>
    )
}


export default Friends;